import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ConfirmationService } from 'primeng/primeng';
import { CycleBean } from './bean/cycle.bean';
import { Message } from 'primeng/primeng';
// import { IsSign, IsRcord, IsOr, IsRf, IsRe } from './../constant/codeValue';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Router } from '@angular/router';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";

@Component({
  selector: 'app-cycle',
  templateUrl: './cycle.component.html',
  styleUrls: ['./cycle.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class Cycle implements OnInit {
  cycleBean: CycleBean = new CycleBean();
  ch: any = CALENDAR_CN;
  date3;
  //table数据
  list: any = [];
  //传入详情的值
  inValueCode: string;
  totalNum: string;
  first: number = 0;//分页控制
  //模态框
  display: boolean = false;
  //1 更新 2添加 3详情
  showModel: string;
  //弹出框的头信息
  headerTitle: string;
  //提示信息
  msgs: Message[] = [];
  total: number = 10;
  isSign: any = [];
  isOr: any = [];
  isRf: any = [];
  isRcod: any = [];
  isRe: any = [];
  code: any;//获取所有码值
  invalDt: Date;
  effDt: Date;
  invalDtStart: Date;
  invalDtEnd: Date;
  tabMesg: any;//表格中无数据时显示的内容
  constructor(
    public httpService: ValuationHttpService,
    public confirmationService: ConfirmationService,
    public commfunc: Commfunc,
    public router: Router
  ) {
    this.codeValues();
    this.isSign = this.code['IsSign'];
    this.isOr = this.code['IsOr'];
    this.isRf = this.code['IsRf'];
    this.isRcod = this.code['IsRcord'];
    this.isRe = this.code['IsRe'];
   }

  ngOnInit() {
    let session = window.sessionStorage;
    if (session.getItem('para')) {
      session.removeItem('para');
    }
    this.cycleBean.pageSize = 10;
    this.cycleBean.pageNum = 1;
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //查询
  querySecle() {
    this.cycleBean.pageSize = 10;
    this.cycleBean.pageNum = 1;
    this.first = 0;
    this.toquery();
  }
   toquery() {
    this.cycleBean.effDt = this.commfunc.transDateN(this.effDt);//生效日期转换
    this.cycleBean.invalDt = this.commfunc.transDateN(this.invalDt);//失效日期转换    
    this.cycleBean.invalDtStart=this.commfunc.transDateN(this.invalDtStart);
    this.cycleBean.invalDtEnd=this.commfunc.transDateN(this.invalDtEnd);

    this.httpService.queryRateRepriceRuleInternalService(this.cycleBean).subscribe(data => {
      if (data.resultList == "" || data.resultList == null) {
        this.tabMesg = tableMessage
      };
      if (data.resultList) {
        this.list = data.resultList
      };
      this.total = data.total;
      // 错误条件查询的提示
      if (data.returnCode.code != "success") {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        this.list = ''
      }
    }, err => {
      this.tabMesg = tableMessage
    })
  }
  //重置
  newSet() {
    this.cycleBean = new CycleBean();
    this.effDt = null;
    this.invalDt = null;
    this.invalDtStart = null;
    this.invalDtEnd = null;
    this.cycleBean.pageSize = 10;
    this.cycleBean.pageNum = 1;
    // this.first = 0;
    // this.toquery();
  }
  //添加
  add(param) {
    this.headerTitle = '周期重定价规则-新增';
    // this.display = true;
    // this.showModel = '2';
    this.router.navigate(['/pages/tzb/valuation/cycle/cycle-add'])

  }
  //新增的回调函数
  addCall(param) {
    this.display = param;
  }
  //详情
  detail(param) {
    this.headerTitle = '周期重定价规则-详情';
    this.showModel = '3';
    this.display = true;
    //  if (param) {
    //   this.inValueCode = JSON.stringify(param);
    // }
    this.inValueCode = param.reprncPlnNum;
    setTimeout(() => {
      this.inValueCode = "";
    }, 1000)
  }
  //详情的回调方法
  detailCall(param) {
    this.display = param;
  }
  //更新
  update(param) {
    this.headerTitle = '周期重定价规则-修改';
    this.showModel = '1';
    this.display = true;
    this.inValueCode = param.reprncPlnNum;
    setTimeout(() => {
      this.inValueCode = "";
    }, 1000)
  }
  //修改的回调函数
  updateCall(param) {
    this.display = false;
    if (param != false) {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '提示', detail: param });
    }
    this.toquery()
  }
  //分页
  paginate(event) {
    //每页显示的条数
    this.cycleBean.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.cycleBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.cycleBean.pageSize;
    //调用查询的方法
    this.toquery();
  }
  //删除
  delete(car) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        //条件
        let param = {
          reprncPlnNum: car.reprncPlnNum,
        };
        this.httpService.deleteRateRepriceRuleService(param).subscribe(data => {
          if (data.returnCode.code == "success") {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            this.first = 0;
            this.toquery();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
        })
      }
    });
  }
   //按钮权限
   permissionCheck(id){
	  return	this.commfunc.permissionCheck(id)
  }

 }


