import { Component, OnInit, Input, Output, EventEmitter, OnChanges, ChangeDetectorRef } from '@angular/core';
import { ConfirmationService } from 'primeng/primeng'
import { CycleBean } from './../bean/cycle.bean';
import { Message } from 'primeng/primeng';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
// import { IsOr, IsRf, IsRm, IsRe } from './../../constant/codeValue';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { checkZzNum, specialSymbol } from 'app/@uisftech/common/service/validator';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';

import * as _ from 'lodash';
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";
@Component({
  selector: 'app-cycle-add',
  templateUrl: './cycle-add.component.html',
  styleUrls: ['./cycle-add.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class CycleAddComponent implements OnInit, OnChanges {
  cycleBean: CycleBean = new CycleBean();
  date3;
  ch: any = CALENDAR_CN;
  //提示信息
  msgs: Message[] = [];
  //传入的值
  @Input() inValue: string;
  //出参
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  //传入详情的值
  inValueCode: string;
  userform: FormGroup; //校验 
  isDisable: boolean = false;
  totalNum: string;
  isOr: any = [];
  isRf: any = [];
  isRm: any = [];
  isRe: any = [];
  effDt: Date;
  invalDt: Date;
  adjPrdCdr: Date;
  taskCategoryId: string;
  isRed: boolean = false;
  changeRed: boolean = false;
  show: boolean = false;
  //设置参考日期类型
  refrDtTpCodeShow: boolean = false;
  //周期校验控制
  adjPrdDtyKeyBoardShow: boolean = false;
  //周年调整日历
  adjPrdCdrShow: boolean = false;
  changeDate: boolean;
  changeDate2: boolean;
  changeDateMax: boolean;
  checkZzNum = checkZzNum;
  code: any;//获取所有码值
  constructor(
    public httpService: ValuationHttpService,
    public router: Router,
    public activatedRoute: ActivatedRoute,
    public commfunc: Commfunc,
    public fb: FormBuilder,
    public changeDetectorRef: ChangeDetectorRef) {
    this.codeValues();
    this.isOr = this.code['IsOr'];
    this.isRf = this.code['IsRf'];
    this.isRm = this.code['IsRm'];
    this.isRe = this.code['IsRe'];
    //审核，工作流
    if (this.activatedRoute.snapshot.params.taskCategoryId) {
      this.taskCategoryId = this.activatedRoute.snapshot.params.taskCategoryId;
    }
  }
  ngOnInit() {
    let session = window.sessionStorage;
    if (session.getItem('para')) {
      session.removeItem('para');
    }
    this.userform = this.fb.group({
      'reprncPlnName': new FormControl('', [Validators.required, specialSymbol, Validators.maxLength(200)]),
      'effDt': new FormControl('', Validators.required),
      'invalDt': new FormControl('', Validators.nullValidator),
      // 'mvmtAdjCode': new FormControl('', Validators.required),
      'prdAdjCode': new FormControl('', Validators.required),
      'refrDtTpCode': new FormControl('', Validators.nullValidator),
      'adjPrdDty': new FormControl('', [Validators.nullValidator]),
      'adjPrdCoCode': new FormControl('', Validators.nullValidator),
      'adjPrdCdr': new FormControl('', Validators.nullValidator),
      'intRateDrcCode': new FormControl('', Validators.required),
      'txInf': new FormControl('', [Validators.maxLength(60), Validators.nullValidator]),
    });
    this.cycleBean.refrDtTpCode = '';
    this.cycleBean.adjPrdCdr = '';
    this.cycleBean.refrDtTpCode = ''
  }
  ngOnChanges() {
    this.cycleBean = new CycleBean();
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }
  //做置灰
  disableText() {
    if (this.cycleBean.prdAdjCode == '0') {
      this.cycleBean.refrDtTpCode = '';
      this.cycleBean['adjPrdDty'] = null;
      this.cycleBean.adjPrdCoCode = '';
      this.cycleBean.adjPrdCdr = '';
      this.show = true;
      this.refrDtTpCodeShow = true;
      this.adjPrdCdrShow = true;
      this.adjPrdDtyKeyBoardShow = false;
    } else {
      this.refrDtTpCodeShow = false;
    }
  }

  //参考日期类型校验
  refrDtTpCodeValidate() {

    if (this.cycleBean.refrDtTpCode == '1') {
      //设置周期数量
      this.cycleBean.adjPrdDty = null;
      //设置周期单位
      this.cycleBean.adjPrdCoCode = "";
      this.show = true;
      this.adjPrdCdrShow = false;
      this.changeRed = false;
    } else {
      if (this.cycleBean.refrDtTpCode == '2' || this.cycleBean.refrDtTpCode == '3' || this.cycleBean.refrDtTpCode == '4' || this.cycleBean.refrDtTpCode == '5') {
        this.show = false;
        this.adjPrdCdrShow = true;
        this.cycleBean.adjPrdCdr = "";
        this.changeRed = false;
      }
    }
    if (!this.cycleBean.refrDtTpCode) {
      this.changeRed = true;
    }
  }

  //校验
  keyboard(event) {
    if (event.target.value.length > 0) {
      this.changeRed = false;
    } else {
      if (event.target.value.length <= 0) {
        this.changeRed = true;
      }
    }
  }
  //校验周期
  adjPrdDtyKeyBoard(event) {
    if (this.cycleBean.prdAdjCode != '0') {
      if (event.target.value.length > 0) {
        this.adjPrdDtyKeyBoardShow = this.checkZzNum(null, event.target.value) != null ? true : false;
      } else {
        this.adjPrdDtyKeyBoardShow = false;
      }
    } else {
      this.adjPrdDtyKeyBoardShow = false;
    }
  }

  //判断周期数量不能为空
  adjPrdDtyIsEmpt() {
    if (!this.cycleBean.adjPrdDty && this.cycleBean.refrDtTpCode && this.cycleBean.refrDtTpCode != '1') {
      return true;
    } else {
      return false;
    }
  }
  //判断周期单位不能为空
  adjPrdCoCodeIsEmpt() {
    if (!this.cycleBean.adjPrdCoCode && this.cycleBean.refrDtTpCode && this.cycleBean.refrDtTpCode != '1') {
      return true;
    } else {
      return false;
    }
  }

  //周年调整日历填写错误提示
  dataCheck(val) {
    let arr1 = [];
    arr1 = val.split(',');
    arr1.forEach((item) => {
      if (item.match(/^(((0[13578]|1[02])-(0[1-9]|[12][0-9]|3[01]))|((0[469]|11)-(0[1-9]|[12][0-9]|30))|(02-(0[1-9]|[1][0-9]|2[0-8]))|\,)$/)) {
        return this.changeDate = false;
      } else {
        return this.changeDate = true
      }
    });
    if (arr1.length > 12) {
      return this.changeDateMax = true
    } else {
      return this.changeDateMax = false
    };
  }
  //日期填写错误提示
  //  dataCheck2(val) {
  //   let arr2=[]; 
  //   // arr2 = val.split(',');
  //   arr2.forEach((item) => {
  //     if(item.match(/^(([0-9]{3}[1-9]|[0-9]{2}[1-9][0-9]{1}|[0-9]{1}[1-9][0-9]{2}|[1-9][0-9]{3})-(((0[13578]|1[02])-(0[1-9]|[12][0-9]|3[01]))|((0[469]|11)-(0[1-9]|[12][0-9]|30))|(02-(0[1-9]|[1][0-9]|2[0-8]))))|((([0-9]{2})(0[48]|[2468][048]|[13579][26])|((0[48]|[2468][048]|[3579][26])00))-02-29)$/)) {
  //       return this.changeDate2 = false;
  //     }  else {
  //       return this.changeDate2 = true
  //     }
  //   });
  // }
  //判断周年调整日历不能为空
  adjPrdCdrIsEmpt() {
    // if (this.cycleBean.adjPrdCdr) {
    //   this.isRed = false;
    // }
    if (!this.cycleBean.adjPrdCdr && this.cycleBean.refrDtTpCode && this.cycleBean.refrDtTpCode == '1') {
      return true;
    } else {
      return false;
    }
  }

  //提交
  submit() {
    if (this.isDisable) {
      return;
    }

    //周年调整日历校验
    if (this.changeDate || this.changeDateMax) {
      return;
    }

    //判断周年调整日历不能为空
    if (!this.cycleBean.adjPrdCdr && this.cycleBean.refrDtTpCode && this.cycleBean.refrDtTpCode == '1') {
      return;
    }

    // 周期调整为是的时候参考日期类型必填
    if (this.cycleBean.prdAdjCode == '1' && this.cycleBean.refrDtTpCode == '') {
      return
    }

    // 周期数量正整数验证
    if (this.cycleBean.prdAdjCode == '1' && this.cycleBean.refrDtTpCode != '1' && this.checkZzNum(null, this.cycleBean.adjPrdDty)) {
      return
    }
    //判断周期数量不能为空
    if (!this.cycleBean.adjPrdDty && this.cycleBean.refrDtTpCode && this.cycleBean.refrDtTpCode != '1') {
      return;
    }
    //判断周期单位不能为空
    if (!this.cycleBean.adjPrdCoCode && this.cycleBean.refrDtTpCode && this.cycleBean.refrDtTpCode != '1') {
      return;
    }

    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    };
    //校验周期
    if (this.cycleBean.prdAdjCode != '0' && !this.cycleBean.prdAdjCode) {
      if (this.cycleBean.adjPrdDty) {
        if (this.checkZzNum(null, this.cycleBean.adjPrdDty)) {
          return;
        }
      }
    }


    //周期调  整下拉控制
    // if (this.cycleBean.prdAdjCode == '1') {
    //   if (!this.cycleBean.adjPrdCdr || this.cycleBean.adjPrdCdr == '') {
    //     this.changeRed = true;
    //     this.show = false;
    //     return;
    //   }
    // }

    //参考日期类型为1 周年调整日历 必填校验 
    // if (this.cycleBean.prdAdjCode != '0') {
    //   if (this.cycleBean.refrDtTpCode == '1') {
    //     if (!this.cycleBean.adjPrdCdr) {
    //       this.isRed = true;
    //       this.changeRed = false;
    //       return;
    //     }
    //   } else {
    //     if (this.cycleBean.refrDtTpCode != '1') {
    //       if (!this.cycleBean.adjPrdDty || !this.cycleBean.adjPrdCoCode) {
    //         this.adjPrdDtyKeyBoardShow = true;
    //         this.changeRed = false;
    //         return;
    //       }
    //     }
    //   }
    // }


    this.isDisable = true;
    //提交服务
    this.cycleBean.effDt = this.commfunc.transDateN(this.effDt);//生效日期转换
    this.cycleBean.invalDt = this.commfunc.transDateN(this.invalDt);//失效日期转换   
    this.cycleBean['taskCategoryId'] = this.taskCategoryId + '';
    this.httpService.addRateRepriceRuleService(this.cycleBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        // this.beanRates = new BeanRates();
        // this.outValue.emit(false);
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
        setTimeout(() => {
          window.history.go(-1);
        }, 1000)
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        this.isDisable = false;
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
      this.isDisable = false;
    })
  }

  //返回
  back() {
    window.history.go(-1);
  }

  // formatDate(date) {
  //   // let y = date.getFullYear();
  //   let m = date.getMonth() + 1;
  //   m = m < 10 ? '0' + m:m;
  //   let d = date.getDate();
  //   d =d < 10 ? ('0' + d) : d;
  //   return m+'-' + d
  // }
}
