import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { Cycle } from "./cycle.component";
import { CycleAddComponent } from "./cycle-add/cycle-add.component";
// import { CycleDetailComponent } from "./cycle-detail/cycle-detail.component";
export const routes: Routes = [
    {
        path: '',component: Cycle,
    },  
    {path:'cycle-add',component:CycleAddComponent},
    // {path:'cycle-detail',component:CycleDetailComponent}
];



export const CycleRouting: ModuleWithProviders = RouterModule.forChild(routes);
