import { Component, OnInit, Input, Output, EventEmitter, OnChanges, ChangeDetectorRef } from '@angular/core';
import { ConfirmationService } from 'primeng/primeng';
import { CycleBean } from './../bean/cycle.bean';
import { Message } from 'primeng/primeng';
// import { IsOr, IsRf, IsRm, IsRe } from './../../constant/codeValue';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import * as moment from "moment"
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { checkZzNum, specialSymbol } from 'app/@uisftech/common/service/validator';
import * as _ from 'lodash';
import { tableMessage, CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { ValuationHttpService } from "app/pages/tzb/valuation/http/valuation.http.service";

@Component({
  selector: 'app-cycle-update',
  templateUrl: './cycle-update.component.html',
  styleUrls: ['./cycle-update.component.scss'],
  providers: [ConfirmationService, Commfunc]
})
export class CycleUpdateComponent implements OnInit, OnChanges {
  cycleBean: CycleBean = new CycleBean();
  date3;
  ch: any = CALENDAR_CN;
  //传入的值
  @Input() inValue: string;
  //出参
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  //传入详情的值
  inValueCode: string;
  //提示信息
  msgs: Message[] = [];
  totalNum: string;
  isOr: any = [];
  isRf: any = [];
  isRm: any = [];
  isRe: any = [];
  data: any;
  effDt: Date;
  invalDt: Date;
  adjPrdCdr: Date;
  adjPrdCdr1: any = null;
  list: any = {};
  old: string;
  userform: FormGroup; //校验 
  isRed: boolean = false;
  changeRed: boolean = false;
  show: boolean = false;
  changeDate: boolean;
  changeDateMax: boolean;
  //设置参考日期类型
  refrDtTpCodeShow: boolean = false;
  //周期校验控制
  adjPrdDtyKeyBoardShow: boolean = false;
  //周年调整日历
  adjPrdCdrShow: boolean = false;
  checkZzNum = checkZzNum;
  isDisable: boolean = false;
  code: any;//获取所有码值
  constructor(
    public httpService: ValuationHttpService,
    public commfunc: Commfunc,
    public fb: FormBuilder,
    public ce: ChangeDetectorRef
  ) {
    this.codeValues();
    this.isOr = this.code['IsOr'];
    this.isRf = this.code['IsRf'];
    this.isRm = this.code['IsRm'];
    this.isRe = this.code['IsRe'];
  }

  ngOnInit() {
    this.userform = this.fb.group({
      'reprncPlnNum': new FormControl('', Validators.required),
      'reprncPlnName': new FormControl('', [Validators.required, specialSymbol, Validators.maxLength(200)]),
      'effDt': new FormControl('', Validators.required),
      'invalDt': new FormControl('', Validators.nullValidator),
      // 'mvmtAdjCode': new FormControl('', Validators.required),
      'prdAdjCode': new FormControl('', Validators.required),
      'refrDtTpCode': new FormControl('', Validators.nullValidator),
      'adjPrdDty': new FormControl('', [Validators.nullValidator]),
      'adjPrdCoCode': new FormControl('', Validators.nullValidator),
      'adjPrdCdr': new FormControl('', Validators.nullValidator),
      'intRateDrcCode': new FormControl('', Validators.required),
      'txInf': new FormControl('', [Validators.maxLength(60), Validators.nullValidator]),
    });
    this.list.adjPrdDty = ""
  }
  ngOnChanges() {
    // let params = {};
    // if (this.inValue) {
    //   params = JSON.parse(this.inValue);
    // }
    if (!this.inValue) {
      return;
    }
    let param = {
      reprncPlnNum: this.inValue,
    };
    //查询方法
    this.httpService.queryRateRepriceRuleByIdService(param).subscribe(data => {
      if (data.resultMap) {
        this.list = data.resultMap;
      }
      this.disableText();
      this.refrDtTpCodeValidate();
      this.keyboard({ target: { value: this.list.refrDtTpCode } });
      this.adjPrdDtyKeyBoard({ target: { value: this.list.adjPrdDty } });
      this.adjPrdDtyIsEmpt();
      this.adjPrdCoCodeIsEmpt();
      this.adjPrdCdrIsEmpt();
      //接收后台日历返回数据
      this.effDt = new Date(this.list.effDt)
      this.invalDt = new Date(this.list.invalDt)
      this.old = this.list.effDt;
    }, err => {
    });
  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }

  //做置灰
  disableText() {
    if (this.list.prdAdjCode == '0') {
      this.list.refrDtTpCode = '';
      this.list['adjPrdDty'] = null;
      this.list.adjPrdCoCode = '';
      this.list['adjPrdCdr'] = '';
      this.show = true;
      this.refrDtTpCodeShow = true;
      this.adjPrdCdrShow = true;
      this.adjPrdDtyKeyBoardShow = false;
    } else {
      // if(this.cycleBean.prdAdjCode == '1'){
      if (this.list.adjPrdDty) {
        if (this.checkZzNum(null, this.list.adjPrdDty)) {
          this.adjPrdDtyKeyBoardShow = true;
        } else {
          this.adjPrdDtyKeyBoardShow = false;
        }
      }
      this.show = false;
      this.refrDtTpCodeShow = false;
      this.adjPrdCdrShow = false;
    }
  }

  //参考日期类型校验
  refrDtTpCodeValidate() {
    if (this.list.refrDtTpCode == '1') {
      //设置周期数量
      this.list.adjPrdDty = null;
      //设置周期单位
      this.list.adjPrdCoCode = "";
      this.show = true;
      this.adjPrdCdrShow = false;
      this.changeRed = false;
    } else {
      if (this.list.refrDtTpCode == '2' || this.list.refrDtTpCode == '3' || this.list.refrDtTpCode == '4' || this.list.refrDtTpCode == '5') {
        this.show = false;
        this.adjPrdCdrShow = true;
        this.list['adjPrdCdr'] = "";
        this.changeRed = false;
      }
    }
    if (!this.list.refrDtTpCode) {
      this.changeRed = true;
    }
  }
  //校验
  keyboard(event) {
    if (event.target.value) {
      this.changeRed = false;
    } else {
      // if (event.target.value.length <= 0) {
      this.changeRed = true;
      // }
    }
  }
  //校验周期
  adjPrdDtyKeyBoard(event) {
    if (this.list.prdAdjCode != '0') {
      if (event.target.value) {
        this.adjPrdDtyKeyBoardShow = this.checkZzNum(null, event.target.value) != null ? true : false;
      } else {
        this.adjPrdDtyKeyBoardShow = false;
      }
    } else {
      this.adjPrdDtyKeyBoardShow = false;
    }
  }

  //判断周期数量不能为空
  adjPrdDtyIsEmpt() {
    if (!this.list.adjPrdDty && this.list.refrDtTpCode && this.list.refrDtTpCode != '1') {
      return true;
    } else {
      return false;
    }
  }
  //判断周期单位不能为空
  adjPrdCoCodeIsEmpt() {
    if (!this.list.adjPrdCoCode && this.list.refrDtTpCode && this.list.refrDtTpCode != '1') {
      return true;
    } else {
      return false;
    }
  }

  //周年调整日历填写错误提示
  dataCheck(val) {
    let arr1 = [];
    arr1 = val.split(',');
    arr1.forEach((item) => {
      if (item.match(/^(((0[13578]|1[02])-(0[1-9]|[12][0-9]|3[01]))|((0[469]|11)-(0[1-9]|[12][0-9]|30))|(02-(0[1-9]|[1][0-9]|2[0-8]))|\,)$/)) {
        return this.changeDate = false;
      } else {
        return this.changeDate = true
      }
    });
    if (arr1.length > 12) {
      return this.changeDateMax = true
    } else {
      return this.changeDateMax = false
    }
  }
  //判断周年调整日历不能为空
  adjPrdCdrIsEmpt() {
    if (!this.list['adjPrdCdr'] && this.list.refrDtTpCode && this.list.refrDtTpCode == '1') {
      return true;
    } else {
      return false;
    }
  }
  //修改
  update() {
    if (this.isDisable) {
      return;
    }

    // 中文名称长度校验
    if (this.list.reprncPlnName.length > 200) {
      return
    }

    // 参考日期类型校验
    if (this.list.prdAdjCode == '1' && this.changeRed) {
      return
    }

    //周年调整日历不能为空
    if (this.list.refrDtTpCode == '1') {
      if (!this.list['adjPrdCdr']) {
        this.isRed = true;
        return;
      }
    }
    //周年调整日历校验
    if (this.changeDate || this.changeDateMax) {
      return;
    }

    // 周期数量正整数验证
    if (this.list.prdAdjCode == '1' && this.list.refrDtTpCode != '1' && this.checkZzNum(null, this.list.adjPrdDty)) {
      return
    }

    //判断周期数量不能为空
    if (!this.list.adjPrdDty && this.list.refrDtTpCode && this.list.refrDtTpCode != '1') {
      return;
    }
    //判断周期单位不能为空
    if (!this.list.adjPrdCoCode && this.list.refrDtTpCode && this.list.refrDtTpCode != '1') {
      return;
    }
    //判断周年调整日历不能为空
    if (this.list['adjPrdCdr'] == '' && this.list.refrDtTpCode && this.list.refrDtTpCode == '1') {
      this.isRed = true;
    } else {
      this.isRed = false;
    }
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    };
    //校验周期
    if (this.list.prdAdjCode != '0' && !this.list.prdAdjCode) {
      if (this.list.adjPrdDty) {
        if (this.checkZzNum(null, this.list.adjPrdDty)) {
          this.adjPrdDtyKeyBoardShow = true;
          return;
        } else {
          this.adjPrdDtyKeyBoardShow = false;
        }
      }
    } else {
      this.adjPrdDtyKeyBoardShow = false;
    }
    //参考日期类型为1 周年调整日历 必填校验 
    // if (this.list.refrDtTpCode == '1') {
    //   if (!this.list['adjPrdCdr']) {
    //     this.isRed = true;
    //     return;
    //   } else {
    //     this.isRed = false;
    //   }
    // } else {
    //   if (this.list.refrDtTpCode != '1') {
    //     if (!this.list.adjPrdDty || !this.list.adjPrdCoCode) {
    //       this.adjPrdDtyKeyBoardShow = false;
    //       return;
    //     }
    //   }
    // }

    //周期调整下拉控制
    // if (this.list.prdAdjCode == '1') {
    //   if (!this.list['adjPrdCdr'] || this.list['adjPrdCdr'] == '') {
    //     this.changeRed = true;
    //     this.show = false;
    //     return;
    //   }
    // }
    //提交服务
    this.list['oldEffDt'] = this.old;
    this.list.effDt = this.commfunc.transDateN(this.effDt);//生效日期转换
    this.list.invalDt = this.commfunc.transDateN(this.invalDt);//失效日期转换 
    // this.list['adjPrdCdr'] = this.commfunc.transDateN(this.adjPrdCdr);//调整周期日历转换 
    this.isDisable = true;
    this.httpService.modifyRateRepriceRuleService(this.list).subscribe(data => {
      // data.returnCode=this.list;
      if (data.returnCode.code == "success") {

        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
        this.outValue.emit(data.returnCode.code);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
        this.isDisable = false;
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
      this.isDisable = false;
    })
  }
  //返回
  back() {
    this.outValue.emit(false);
  }
}
